.fadetext {
    li {
        list-style: none;
        opacity: 0;

        &.show {
            animation: fadetext_show 1s forwards ease-in-out;
        }
    }
}

@keyframes fadetext_show {
    0% {
        opacity: 0;
        transform: scaleY(0.5);
    }

    100% {
        opacity: 1;
        transform: scaleY(1);
    }
}
