.cpr {
    .ui {
        position: absolute;
        height: 0;
        z-index: 2;

        .intro {
            position: absolute;
            left: 0;
            top: 0;
            color: white;
            background-color: black;
            will-change: transform, clip-path, opacity;
            display: flex;
            flex-direction: column;
            align-items: center;
            overflow: hidden;

            .bg {
                width: 100%;
                z-index: -1;
                position: absolute;
                top: 50%;
                transform: translate(0, -50%);
            }

            &.zoom {
                animation: intro_zoom_out 2s forwards ease-in-out;
            }

            .fadetext {
                position: absolute;
                top: 0.3rem;
                text-align: center;
                font-size: 0.14rem;
                line-height: 0.24rem;
            }

            .start {
                position: absolute;
                bottom: 1rem;
                width: 1.4rem;
                height: 0.4rem;
                font-weight: bold;
                text-align: center;
                line-height: 0.4rem;
                border-radius: 0.4rem;
                opacity: 0;
                pointer-events: none;
                transition: opacity 1s ease-in-out;
                border: 1px solid #999;
                background: #000;
                color: #fff;
                font-size: 0.2rem;

                &.show {
                    opacity: 1;
                    pointer-events: all;
                }

                &:active {
                    background: white;
                    color: black;
                }
            }
        }

        .status {
            .heartbeat {
                width: 2.75rem;
                height: 0.8rem;
                position: fixed;
                left: 0;
                top: 0;
            }

            .time_past {
                position: fixed;
                right: 0;
                top: 0;
                width: 1rem;
                text-align: center;
                margin-top: 0.15rem;
                font-size: 0.18rem;
            }

            .taptap {
                position: fixed;
                left: 0;
                bottom: 0;
                width: 3.75rem;
            }

            .operate_btn {
                position: fixed;
                width: 1.25rem;
                height: 0.5rem;
                border-radius: 0.2rem;
                background: #fff;
                font-size: 0.18rem;
                left: 1.25rem;
                bottom: 0.1rem;
                border: 1px solid #999;
                z-index: 1;
            }
        }
    }

    .canvas {
        position: absolute;
        z-index: 1;
    }
}

.modal_text {
    display: flex;
    flex-direction: column;
    background: #fff;
    margin: 0 0.3rem;
    overflow: hidden;
    border-radius: 0.2rem;
    margin-top: 1rem;
    padding: 0.2rem;

    .start {
        text-align: center;
        font-size: 0.24rem;
        font-weight: bold;
        margin-bottom: 0.1rem;
    }

    .title {
        font-size: 0.18rem;
        font-weight: bold;
        line-height: 0.35rem;
    }

    .desc {
        font-size: 0.14rem;
        list-style: none;
        line-height: 0.2rem;
    }

    button {
        align-self: center;
        width: 1.8rem;
        height: 0.4rem;
        background: #fff;
        border: none;
        outline: none;
        line-height: 0.4rem;
        font-size: 0.2rem;
        border-radius: 0.4rem;
        border: 1px solid #999;
        margin-top: 0.2rem;
    }
}

@keyframes intro_zoom_out {
    0% {
        clip-path: circle(100vw at center);
        opacity: 1;
    }

    50% {
        clip-path: circle(50vw at center);
        opacity: 1;
    }

    70% {
        clip-path: circle(50vw at center);
        opacity: 1;
    }

    100% {
        clip-path: circle(1vw at center);
        opacity: 0.3;
    }
}
